const InitialState = () => {
	return {
		game: {
			data: [],
			hidden: true,
			status: "stop",
			result: [],
		},
		settings: {
			start: 0,
			stop: 9,
			interval: 6,
			practice: false,
		},
	}
}

export default InitialState



